html {
  body {
    width: 100%;
    height: 100%;
    .container {
      width: 100%;
      max-width: 500px;
      height: 600px;
      display: flex;
      flex-direction: column ;
      margin: 0 auto;
      .npc, .me, .message {
        height: calc( 100% / 3);
      }
      .npc{
        border: 1px solid red;
        display: flex;
        flex-direction: row ;
        width: 100%;
        // height: 100%;
        margin: 0;
        padding: 0;
        .left,.right {
          width: 50%;
          height: 100%;
          border: 1px solid blue;
        }
        .left{
          .blood {
            width: calc(100% - 10px);
            height: 60px;
            h4 {
              margin: 5px 0;
              padding-left: 10px;
              letter-spacing: 8px;
            }
            div {
              position: absolute;
              left: 10px;
              bottom: 0;
              margin: 5px 0;
              width: calc( 100% - 100px );
              border-bottom: 5px solid black;
              border-left: 5px solid black;
              border-radius: 0 0 0 5px;
              span:first-child {
                display: inline-block;
                width: 30px;        
                &:before {
                  content: 'HP: ';
                  padding-left: 5px;
                  font-weight: bolder;
                }
              }
              span:nth-child(2) {
                display: inline-block;
                width: calc( 100% - 50px );
                border-radius: 10px;
                border: 1px solid black;
                background: red;
                height: 15px;
              }
            }
          }
          .top {
            position:relative;
            top: 0;
            left: 0;
            padding-left: 10px;
            border: 1px solid green;
          }
        }
        .right {
          .top {
            position:relative;
            top: 0;
            left: 0;
            padding-right: 10px;
            
            border: 1px solid green;
          }
          .baby {
            width: calc(100% - 10px);
            height: 250px;
            .images{
              display: inline-block;
              text-align: center;     
              width: 100%;
              height: 50%;
              border: 1px solid green;
              img {                           
                max-width: 100%;
                width: auto;
                max-height: 100%;
                height: auto;
              }
            }
          }
        }
      }
